@use './fonts' as *;
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 24px;
  background-color: var(--lightgrayishcyanactive);
  font-family: "Space Mono", monospace;
}
input,
textarea,
select {
  font-family: inherit;
}
