:root {
  --font-body: "Space Mono", monospace;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --strongcyan: hsl(172, 67%, 45%);
  --verydarkcyan: hsl(183, 100%, 15%);
  --darkgrayishcyanactive: hsl(186, 14%, 43%);
  --darkgrayishcyan: hsl(184, 14%, 56%);
  --lightgrayishcyan: hsl(189, 41%, 97%);
  --lightgrayishcyanactive: hsl(185, 41%, 84%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 24px;
  background-color: var(--lightgrayishcyanactive);
  font-family: "Space Mono", monospace;
}

input,
textarea,
select {
  font-family: inherit;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 90rem;
  margin: 0 auto;
}
@media (max-width: 54.1875em) {
  .container {
    margin: 0 auto;
  }
}
@media (max-width: 23.4375em) {
  .container {
    max-width: 23.4375rem;
    margin: 0 auto;
    padding: 0;
  }
}

.navbar {
  display: block;
  margin: 8rem auto 4rem auto;
  padding: 0;
  text-align: center;
}
@media (max-width: 23.4375em) {
  .navbar {
    margin: 3rem auto 1.5rem auto;
  }
}

form {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
@media (max-width: 54.1875em) {
  form {
    margin: 0 auto;
  }
}
@media (max-width: 23.4375em) {
  form {
    margin: 0;
  }
}

.calc .calc-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2.5rem;
  margin: auto;
  width: 50rem;
  height: 25rem;
  background-color: var(--white);
  border-radius: 25px;
}
@media (max-width: 54.1875em) {
  .calc .calc-container {
    grid-template-columns: 1fr;
    margin: 0 auto;
    max-width: 23.4375rem;
    height: 45rem;
    gap: 0;
  }
}
@media (max-width: 23.4375em) {
  .calc .calc-container {
    margin: 0;
  }
}
.calc .calc-container .calc-inputs {
  margin: 2rem;
  margin-right: 0;
  align-content: center;
  height: 28.125rem;
}
@media (max-width: 54.1875em) {
  .calc .calc-container .calc-inputs {
    margin: 0 2rem;
    height: 20.625rem;
  }
}
.calc .calc-container .calc-inputs input[name=bill] {
  display: block;
  -moz-appearance: textfield;
  margin: 0 auto;
  padding: 0.5rem;
  width: 100%;
  background-color: var(--lightgrayishcyan);
  border: none;
  border-radius: 5px;
  color: var(--verydarkcyan);
  font-size: 1.2rem;
  font-weight: 700;
  text-align: right;
  outline: none;
}
@media (max-width: 54.1875em) {
  .calc .calc-container .calc-inputs input[name=bill] {
    width: 100%;
    height: 3rem;
    font-size: 1.5rem;
  }
}
.calc .calc-container .calc-inputs .bill {
  width: 100%;
  color: var(--darkgrayishcyan);
  font-size: 0.8rem;
  font-weight: 700;
  margin-top: 2rem;
  margin-bottom: 0.5rem;
}
.calc .calc-container .calc-inputs .dollar {
  position: relative;
  top: -42px;
  left: 15px;
}
.calc .calc-container .calc-inputs .calc-tip-title {
  color: var(--darkgrayishcyan);
  font-size: 0.8rem;
}
.calc .calc-container .calc-inputs .calc-percs {
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 1rem;
}
@media (max-width: 54.1875em) {
  .calc .calc-container .calc-inputs .calc-percs {
    grid-template-columns: repeat(2, auto);
    width: 100%;
  }
}
.calc .calc-container .calc-inputs .calc-percs [type=radio] {
  -moz-appearance: textfield;
  position: absolute;
  top: 0;
  left: -10px;
  width: 1rem;
  height: 1rem;
  padding: 0;
  margin: 0;
  z-index: -1;
}
.calc .calc-container .calc-inputs .calc-percs [name=option-custom-input] {
  -moz-appearance: textfield;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 105px;
  font-size: 1.5rem;
  font-weight: 700;
  background-color: var(--lightgrayishcyanactive);
  color: var(--verydarkcyan);
  text-align: center;
  border: none;
  border-radius: 5px;
  text-align: end;
}
.calc .calc-container .calc-inputs .calc-percs [name=option-custom-input]::placeholder {
  text-align: center;
}
@media (max-width: 54.1875em) {
  .calc .calc-container .calc-inputs .calc-percs [name=option-custom-input] {
    width: 100%;
  }
}
.calc .calc-container .calc-inputs .calc-percs .option-label {
  text-align: center;
  width: 105px;
  position: relative;
  background-color: var(--verydarkcyan);
  color: var(--white);
  font-size: 1rem;
  font-weight: 700;
  border-radius: 5px;
  padding: 0.5rem 0;
  margin: 0;
  cursor: pointer;
}
.calc .calc-container .calc-inputs .calc-percs .option-label:nth-child(6) {
  background-color: var(--lightgrayishcyan);
  color: var(--verydarkcyan);
}
@media (max-width: 54.1875em) {
  .calc .calc-container .calc-inputs .calc-percs .option-label {
    width: 100%;
  }
}
.calc .calc-container .calc-inputs .calc-percs .option-label:hover {
  background-color: var(--lightgrayishcyanactive);
  color: var(--verydarkcyan);
}
.calc .calc-container .calc-inputs .calc-percs .option-label.active {
  background-color: var(--strongcyan);
  color: var(--verydarkcyan);
}
.calc .calc-container .calc-inputs .calc-percs h2 {
  display: inline;
  margin: 0;
  padding: 0;
  text-align: center;
}
.calc .calc-container .calc-inputs .calc-percs #option-custom-label {
  position: relative;
  top: 0;
  left: 0;
  cursor: pointer;
}
@media (max-width: 54.1875em) {
  .calc .calc-container .calc-inputs .calc-percs #option-custom-label {
    width: 100%;
  }
}
.calc .calc-container .calc-inputs .people-wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
  margin-bottom: 0.5rem;
}
.calc .calc-container .calc-inputs .people-wrapper .calc-number-of-people {
  color: var(--darkgrayishcyan);
  font-size: 0.8rem;
  width: fit-content;
  font-weight: 700;
}
.calc .calc-container .calc-inputs .people-wrapper .zero-people {
  display: flex;
  font-size: 0.8rem;
  width: fit-content;
  color: red;
  opacity: 0;
}
.calc .calc-container .calc-inputs .calc-people {
  display: block;
  -moz-appearance: textfield;
  margin: auto;
  padding: 0.5rem;
  width: 100%;
  background-color: var(--lightgrayishcyan);
  border: none;
  border-radius: 5px;
  color: var(--verydarkcyan);
  font-size: 1.2rem;
  font-weight: 700;
  text-align: right;
  outline: none;
}
@media (max-width: 54.1875em) {
  .calc .calc-container .calc-inputs .calc-people {
    margin: auto;
  }
}
.calc .calc-container .calc-inputs .people-icon {
  position: relative;
  left: 15px;
  top: -42px;
}
.calc .calc-container .calc-visor {
  max-width: 23.4375rem;
  padding: 2rem;
  border: none;
  border-radius: 25px;
  margin: 2rem;
  margin-left: 0;
  align-content: center;
  height: 350px;
  background-color: var(--verydarkcyan);
}
@media (max-width: 54.1875em) {
  .calc .calc-container .calc-visor {
    margin: 7rem 2rem;
    height: 15.625rem;
  }
}
.calc .calc-container .calc-visor .calc-tip-amount {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.calc .calc-container .calc-visor .calc-tip-amount h1 {
  margin: 0;
  font-size: 2.5rem;
  color: var(--strongcyan);
}
.calc .calc-container .calc-visor .calc-tip-amount-title {
  font-size: 0.8rem;
  color: var(--white);
  margin: 0;
  width: 90px;
}
.calc .calc-container .calc-visor .calc-tip-amount-person {
  font-size: 0.7rem;
  color: var(--darkgrayishcyan);
}
.calc .calc-container .calc-visor .calc-total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 3rem;
}
.calc .calc-container .calc-visor .calc-total h1 {
  margin: 0;
  font-size: 2.5rem;
  color: var(--strongcyan);
}
.calc .calc-container .calc-visor .calc-total-title {
  font-size: 0.8rem;
  color: var(--white);
  margin: 0;
  width: 90px;
}
.calc .calc-container .calc-visor .calc-total-person {
  font-size: 0.7rem;
  color: var(--darkgrayishcyan);
}
.calc .calc-container .calc-visor .reset {
  display: block;
  margin: 0 auto;
  margin-top: 4rem;
  width: 100%;
  position: relative;
  top: 47px;
  left: 0;
  height: 2.5rem;
  border: none;
  border-radius: 5px;
  background-color: var(--darkgrayishcyan);
  opacity: 0.3;
  color: var(--verydarkcyan);
  font-weight: 900;
  font-size: 1rem;
  letter-spacing: 0.2mm;
  cursor: pointer;
}
@media (max-width: 54.1875em) {
  .calc .calc-container .calc-visor .reset {
    top: -40px;
    background-color: var(--strongcyan);
    opacity: 1;
  }
}
.calc .calc-container .calc-visor .reset:hover {
  background-color: var(--lightgrayishcyanactive);
  color: var(--verydarkcyan);
}

footer {
  text-align: center;
  font-size: 0.8rem;
  margin: 1rem;
}